.blog-container{
  display: flex;
  flex-direction: column;
  padding-top: 25px;
  height: 100%;
  width: 100%;
 
  flex:4;

  h1 {
    color:#D23EFF;
    font-size: 36pt;
    margin-bottom: 50px;
    text-align: center;
  }

  .blog-posts{
    flex:1;
    
    text-decoration: underline;
    color:greenyellow;
    width:100%;
    
   
    
    #post {
      display:flex;
      flex-direction: column;
      text-align: center;
      align-self: center;
      
      
    h2 {
      flex:1;
     
      align-self: center;
      cursor: pointer;
    }
    img {
      flex:1;
      margin:25px 0 25px 0;
      align-self: center;
      width: 50%;
    }
  }
  }
}